<template>
  <div id="app">
    <section class="hero is-success is-bold is-fullheight">
      <!-- Hero head: will stick at the top -->
      <div class="hero-head py-3 px-4">
        <header class="navbar">
          <MyNavbar :user="user" @logout="logout" />
        </header>
      </div>

      <!-- Hero content: will be in the middle -->
      <div class="hero-body py-1 px-1">
        <div class="container has-text-centered">
          <router-view />
        </div>
      </div>

      <!-- Hero footer: will stick at the bottom -->
      <div class="hero-foot py-3">
        <MyFooter :alias="alias" />
      </div>
    </section>
    <!-- <MyHeader />-->
  </div>
</template>

<script>
import MyNavbar from "@/components/MyNavbar.vue";
import MyFooter from "@/components/MyFooter.vue";
import { auth } from "@/firebase/init";
import { getUserAlias } from "@/helpers/getUserAlias.js";

export default {
  components: {
    MyNavbar,
    MyFooter
  },
  data() {
    return {
      user: null,
      alias: null
    };
  },
  methods: {
    logout() {
      auth
        .signOut()
        .then(() => {
          this.$router.push({ name: "Login" });
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  created() {
    auth.onAuthStateChanged(user => {
      if (user) {
        this.user = user;
        // получаю псевдоним(alias) текущего  пользователя
        getUserAlias(user.uid).then(alias => (this.alias = alias));
      } else {
        this.user = null;
      }
    });
  }
};
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>


