<template>
  <div id="map"></div>
</template>

<script>
function resize() {
  // родитель карты в layout(доступный размер, с отступами, для карты)
  let heroBody = document.querySelector(".hero-body");

  // элем карты
  let lMap = document.querySelector("#map");

  lMap.style.height = "";

  // высота родителя
  let heroBodyHeight = heroBody.clientHeight;

  // отступы родителя
  let heroBodyVerticalPaddings =
    parseInt(window.getComputedStyle(heroBody).paddingTop) +
    parseInt(window.getComputedStyle(heroBody).paddingBottom);

  // новая высота для элем карты в px
  lMap.style.height = heroBodyHeight - heroBodyVerticalPaddings + "px";
}

import "leaflet/dist/leaflet.css";
import L from "leaflet";
import { auth, db } from "@/firebase/init";

export default {
  data() {
    return {
      mapInstance: null,
      lat: 51.505,
      lng: -0.09
    };
  },
  methods: {
    removeMapInstance() {
      this.mapInstance.remove();
    },
    createMapInstance() {
      // initialize the map on the "map" div with a given center and zoom
      const map = L.map("map", { preferCanvas: true }).setView(
        [this.lat, this.lng],
        13
      );

      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution:
          '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
      }).addTo(map);

      // add markers
      // marker icon
      var redIcon = L.icon({
        iconUrl: "leaflet-maps-red-marker-vector.svg",
        iconSize: [58, 48] // size of the icon
      });

      // проверка наличия в док пользователей координат и вывод по ним маркеров
      db.collection("geoUsers")
        .get()
        .then(users => {
          users.docs.forEach(doc => {
            let data = doc.data();
            if (data.geolocation) {
              const marker = L.marker(
                [data.geolocation.lat, data.geolocation.lng],
                {
                  icon: redIcon,
                  opacity: 0.7,
                  title: doc.id
                }
              ).addTo(map);

              marker.addEventListener("click", () =>
                // console.log("doc.id", doc.id)
                this.$router.push({
                  name: "UserProfile",
                  params: { id: doc.id }
                })
              );
            }
          });
        });

      return map;
    },
    renderMap() {
      // инстанс карты, удалить его при закрытии компонента, this.mapInstance.remove();
      this.mapInstance = this.createMapInstance();
    }
  },
  mounted() {
    resize();
    window.addEventListener("resize", resize);

    // get current user
    let user = auth.currentUser;
    // console.log(user);

    // get user geolocation
    if (window.navigator) {
      window.navigator.geolocation.getCurrentPosition(
        position => {
          this.lat = position.coords.latitude;
          this.lng = position.coords.longitude;

          // find the user record and then update geocoords
          db.collection("geoUsers")
            .where("user_id", "==", user.uid)
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                db.collection("geoUsers")
                  .doc(doc.id)
                  .update({
                    geolocation: {
                      lat: this.lat,
                      lng: this.lng
                    }
                  });
              });
            })
            .then(this.renderMap())
            .catch(function(error) {
              console.log("Error getting documents: ", error);
            });
        },
        err => {
          console.log(err);
          this.renderMap();
        }
      );
    } else {
      this.renderMap();
    }
  },
  beforeDestroy() {
    this.removeMapInstance();
    window.removeEventListener("resize", resize);
  }
};
</script>

<style lang="scss" scoped></style>
