<template>
  <div class="columns is-centered">
    <div class="column is-6">
      <h3 class="title">Signup</h3>
      <form @submit.prevent="signup" class="box">
        <div class="field has-text-left">
          <label class="label">Email:</label>
          <div class="control has-icons-left has-icons-right">
            <input
              class="input is-success"
              type="email"
              placeholder="email input..."
              v-model="email"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-envelope"></i>
            </span>
            <span class="icon is-small is-right">
              <i class="fas fa-exclamation-triangle"></i>
            </span>
          </div>
        </div>
        <div class="field has-text-left">
          <label class="label">Password:</label>
          <p class="control has-icons-left">
            <input
              class="input is-success"
              type="password"
              placeholder="password..."
              v-model="password"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-lock"></i>
            </span>
          </p>
        </div>
        <div class="field has-text-left">
          <label class="label">Alias:</label>
          <div class="control has-icons-left has-icons-right">
            <input class="input is-success" type="text" placeholder="alias..." v-model="alias" />
            <span class="icon is-small is-left">
              <i class="fas fa-user"></i>
            </span>
            <span class="icon is-small is-right">
              <i class="fas fa-check"></i>
            </span>
          </div>
        </div>
        <div class="field">
          <p class="help is-danger">{{feedback}}</p>
        </div>
        <div class="field">
          <p class="control">
            <button class="button is-primary">Submit</button>
          </p>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import slugify from "slugify";
import { db, auth } from "@/firebase/init";

export default {
  name: "Register",
  data() {
    return {
      auth,
      email: null,
      password: null,
      alias: null,
      feedback: null,
      slug: null
    };
  },
  methods: {
    signup() {
      if (this.email && this.password && this.alias) {
        this.slug = slugify(this.alias, {
          replacement: "-",
          remove: /[*+~.()'"!:@]/g,
          lower: true
        });

        let ref = db.collection("geoUsers").doc(this.slug);
        ref.get().then(doc => {
          if (doc.exists) {
            this.feedback = "This alias already exist";
          } else {
            this.feedback = "This alias is free to use";
            auth
              .createUserWithEmailAndPassword(this.email, this.password)
              .then(({ user }) => {
                ref.set({
                  alias: this.alias,
                  geolocation: null,
                  user_id: user.uid
                });
              })
              .then(() => {
                this.$router.push({ name: "Home" });
              })
              .catch(error => (this.feedback = error.message));
          }
        });
      } else {
        this.feedback = "You mast enter all fields";
      }
    }
  }
};
</script>

<style lang="scss" scoped></style>
