import { db } from "@/firebase/init";

// получение псевдонима(alias) текущего пользователя
const getUserAlias = async (uid) => {
  let alias = "null";

  await db
    .collection("geoUsers")
    .where("user_id", "==", uid)
    .get()
    .then((querySnapshot) => {
      querySnapshot.forEach((doc) => {
        alias = doc.data().alias;
      });
    })
    .catch(function(error) {
      console.log("Error getting documents: ", error);
    });

  return alias;
};

export { getUserAlias };
